
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body {
  margin:0px;
  padding:0px;
  font-family: 'Noto Serif SC', serif;
}
html,body{
  height:100%;
}
.black {
  width:100%;
  height:100%;
  background-color:rgba(0,0,0,0.5);
  position:fixed;
  top:0;
  left:0;
  z-index:99;
}
a {
  text-decoration:none;
}
#top {
  background-color:#444;
  color:#fff;
  height:50px;
}
#main {
  height:100%;
}
.d1 {
  height:calc(100% - 50px);
}
.left {
  width:200px;
  height:100%;
  float:left;
  position:relative;
}
.left-d {
  width:200px;
  height:100%;
  background-color:#ddd;
  font-size:25px;
  position:relative;
  z-index:3;
}
.left-d-d {
  height:50px;
  line-height:50px;
  background-color:#ddd;
  color:#000;
  text-align:center;
}
.left-d-d:hover {
  background-color:#444;
  color:#fff;
}
.left-d-d-on {
  background-color:#444;
  color:#fff;	
}
.left-d-more {
  width:200px;
  height:100%;
  background-color:#444;
  color:#fff;
  font-size:25px;
  -webkit-box-shadow:0 0 5px #222;
          box-shadow:0 0 5px #222;
  position:absolute;
  top:0;
  left:0;
  z-index:2;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  -webkit-transition-duration:0.5s;
          transition-duration:0.5s;
}
.left-d-more a {
  color:#fff;
}
.left-d-more-show {
  left:200px;
}
.left-d-more-d {
  height:50px;
  line-height:50px;
  font-size:15px;
  text-align:center;
}
.left-d-more-d:hover {
  background-color:#ddd;
  color:#000;
}
.d1-r {
  width:calc(100% - 200px);
  height:100%;
  background-color:#eee;
  float:left;
  padding:0 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size:22px;
  overflow-y:scroll;
}
.list {	
}
.list-d {
  height:50px;
  line-height:50px;
  padding:10px;
  background-color:#444;
  color:#fff;
  margin:0 0 15px 0;
}
.page {
  float:right;
}
.page-d {
  width:30px;
  height:30px;
  line-height:30px;
  text-align:center;
  border-style: solid;
  border-width: 1px; 
  border-radius:100%;
  float:left;
  margin:0 10px 0 0;
  font-size:12px;
}
.dispatch-table {
  width:14200px;
  border-collapse:collapse;
}
.dispatch-table th {
  width:200px;
}
.dispatch-table td {
  width:200px;
}
.dispatch-table-d {
  background-color:#f00;
  text-align:center;
  margin:0 0 2px 0;
}